import React,{ useState } from 'react';
import * as PIXI from "pixi.js";
import { loader } from '../DataLoader';
import { Container, Graphics, Sprite, Text } from '@inlet/react-pixi/animated';
import { Spring } from 'react-spring/renderprops';

export const Guide = ({ props })=> {
  const { setOpenGuide } = props;
  const [ guideStep, setGuideStep ] = useState(0);
  const [ textRest, setTextRest ] = useState(false);
  const guideList = [
    {
      title:'',
      content:'///'
    },{
      title:'',
      content:'[]'
    },{
      title:'',
      content:'//[]'
    },{
      title:'',
      content:'()'
    },{
      title:'',
      content:'(**)'
    },
  ]
  return <Graphics
    x={400}
    y={300}
    zIndex={99}
    draw={g=> {
      g.clear();
      g.beginFill(`0x011627`);
      g.drawRoundedRect(-400,-300,800,600,0);
      g.endFill();
    }}
  >
    <Container sortableChildren={true}>
      <Text
        text={guideList[guideStep].title}
        anchor={{y:0.5}}
        x={-270}
        y={-245}
        style={new PIXI.TextStyle({ fontFamily: '"Source Sans Pro", Helvetica, sans-serif',
          fontSize: 28,
          fill:['#ffffff', '#00bcd4'],
        })}
      />
      <Sprite
        anchor={0.5}
        x={0}
        y={0}
        width={540}
        height={405}
        image={loader.resources[`guide-${guideStep+1}`].data}
      />
      <Spring
        from={{ x:0, y:250, alpha:0 }}
        to={{ x:0, y:230, alpha:1 }}
        config={{ duration: 250 }}
        reset={textRest}
        onStart={()=>setTextRest(false)}
      >
        {props =>
          <Text
            text={guideList[guideStep].content}
            anchor={{x:0.5}}
            style={new PIXI.TextStyle({ fontFamily: '"Source Sans Pro", Helvetica, sans-serif',
              fontSize: 16,
              fill:['#ffffff', '#00bcd4'],
              breakWords: true,
              wordWrap: true,
              wordWrapWidth:500
            })}
            {...props}
          />
        }
      </Spring>
      <Text
        text={guideStep===4?'':''}
        anchor={{y:0.5}}
        x={guideStep===4?265:250}
        y={255}
        style={new PIXI.TextStyle({ fontFamily: '"Source Sans Pro", Helvetica, sans-serif',
          fontSize: 18,
          fill:['#ffffff', '#ffffff'],
        })}
      />
      <Sprite
        width={40}
        height={30}
        anchor={0.5}
        x={335}
        y={260}
        interactive={true}
        buttonMode={true}
        image={loader.resources[`next`].data}
        pointertap={()=>{
          if(guideStep===4){ 
            setOpenGuide(false);
            return; 
          }else {
            setGuideStep(prev=>prev+=1);
            setTextRest(true);
          };
        }}
      />
    </Container>
  </Graphics>
};